.pr {
   background-color: #A9C9FF;
   background-image: linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%);
}

.inmain-prima {
   grid-template-rows: 64px 64px 1fr;
   grid-template-columns: 64px 300px 64px;
   position: absolute;
   max-width: 100%;
   justify-content: center;
   /* padding-top: 5vh; */
   height: 100%;
   width: 100%;
   /* box-sizing: border-box; */
   display: grid;
   gap: 8vh 8px;
}

.e-note-prima {
   grid-row-start: 3;
   grid-row-end: 4;
   grid-column-start: 3;
   grid-column-end: 4;
   margin-top: -2vh;
}

.a-note-prima {
   grid-row-start: 2;
   grid-row-end: 3;
   grid-column-start: 3;
   grid-column-end: 4;
   margin-top: 1vh;
}

.d-note-prima {
   /* transform: translateY(150%); */
   grid-row-start: 2;
   grid-row-end: 3;
   grid-column-start: 1;
   grid-column-end: 2;
   margin-top: 3vh;
}

.g-note-prima {
   /* transform: translateY(150%); */
   grid-row-start: 3;
   grid-row-end: 4;
   grid-column-start: 1;
   grid-column-end: 2;
   /* margin-top: 12vh; */
}





.main {
   grid-row: 2/3;
   grid-column: 2/3;
   width: 100%;
   height: 100%;
   position: relative;
   /* z-index: 6; */
   /* width: 100%;
   height:100%; */
}


@media screen and (max-width: 500px) {
.inmain-prima {
   grid-template-rows: 40px 40px 1fr;
   grid-template-columns: 40px 200px 40px;
}
}

